<template>
  <BaseLayout title="我的售后">

    <van-search v-model="state.oid" placeholder="请输入订单编号" background="#FFF" input-align="left" @clear="onSearch" @search="onSearch" />

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        
        <van-row class="block" align="center" v-for="(item,index) in state.list" :key="index">
          <van-col span="12" class="mall" @click="$copy(item.id, false)">{{item.mall}}</van-col>
          <van-col span="12" class="result"><van-tag round :color="item.color">{{item.result}}</van-tag></van-col>
          <van-col span="24" class="detail">
            <van-card :thumb="item.image" @click="$router.push('/my/order/detail?id=' + item.oid)">
              <template #title>
                <div class="title van-multi-ellipsis--l3">{{item.title}}</div>
              </template>
              <template #desc>
                <van-cell class="desc" :title="item.reason" :value="'x ' + item.num" :border="false"></van-cell>
              </template>
              <template #footer>
                <van-button :type="item.status == 0 ? 'default' : 'primary'" size="mini" :plain="item.status > 0" round @click.stop="$router.push('/my/service/detail?id=' + item.id + '&oid=' + item.oid + '&sku=' + item.sku)">{{ item.status == -1 ? "申请售后" : "查看详情"}}</van-button>
                <van-button type="danger" size="mini" v-show="item.status == 5 && (item.type == 2 || item.type == 3)" round @click.stop="$router.push('/my/service/detail?oid=' + item.oid + '&sku=' + item.sku)">再次售后</van-button>
              </template>
            </van-card>
          </van-col>
        </van-row>

      </van-list>
    </van-pull-refresh>

  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
  padding: 5px;
  background: #F9F9F9;
}
.list .block {
  margin-bottom: 15px;
  padding: 10px;
  background: #FFF;
  border-radius: 10px;
  border: solid 1px #F6F6F6;
  font-size: 12px;
}

.list .mall {
  height: 24px;
  color: #F66;
  font-size: 14px;
  font-weight: 600;
}
.list .result {  
  height: 24px;
  text-align: right;
}
.list .detail {
  float: left;
  width: 100%;
  height: auto;
  overflow-x: scroll;
}
.list .title {
  line-height: 22px;
}
.list .desc {
  background: transparent;
  color: #F66;
}
</style>

<script>
import {reactive,ref} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  props: {
    id: {
      type: [String, Number],
      default: "",
    },
  },

  setup() {
    const state = reactive({
      oid: "",
      refreshing: false,
      loading: false,
      finished: false,
      list: [],
      offset: 0,
      limit : 20,
    });
    return {state};
  },

  created(){
    this.state.oid = this.id; 
  },

  mounted(){
    this.onRefresh();
  },

  computed: {
    ...mapState(["channel"]),
  },

  methods: {
    onRefresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/mall/serviceList?id=" + this.state.oid + "&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            mall: this.channel[item.channel]["name"],
            reason: (item.status <= 0 ? "" : ["退货", "换货", "维修"][item.type]),
            result: (item.status == 9 ? "审核未通过" : ["申请被关闭", "申请中", "已审核", "退件中", "返件中", "已完成"][item.status]),
            color: ["#CCC", "#066", "#00C", "#C00", '#C0C', "#0C0"][item.status],
          }});
        }
      });
    },
    onSearch(){
      this.$router.replace('?id=' + this.state.oid);
      this.onRefresh();
    },
  }
}
</script>
